  padding: 0 50px;
  margin: 0 auto;
  box-sizing: border-box; }
  .wrapper--banner {
    max-width: 1840px;
    padding: 0 100px; }
  .wrapper--full-height {
    height: 100%; }

.title {
  margin-bottom: 65px;
  font-size: 56px;
  line-height: 73px;
  font-weight: bold;
  color: #2C2C2C; }

.button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 64px;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #00A76D;
  border: 1px solid #00A76D;
