  cursor: pointer;
  outline: none;
  transition: 0.5s; }
  .button:hover {
    color: #00A76D;
    background-color: #ffffff; }
  .button:active {
    opacity: 0.5;
    transition: 0.1s; }
  .button:focus {
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2); }
  .button--banner {
    width: 240px;
    font-size: 16px;
    line-height: 21px;
    color: #2C2C2C;
    background-color: #ffffff;
    border: 1px solid #2E60C0;
    text-transform: none; }
    .button--banner:hover {
      color: #ffffff;
      background-color: #2E60C0;
      border: 1px solid #ffffff; }

.button--modal-open {
  margin-bottom: 30px; }

.ref {
  margin-bottom: 80px;
  padding-top: 90px; }
  .ref__slider {
    display: flex;
    justify-content: space-between;
    border-bottom: 7px solid #F7F8FA; }
  .ref__slide {
    display: flex; }
  .ref__box {
    position: relative;
    padding-bottom: 24px;
    font-weight: bold;
    font-size: 18px;
    line-height: 23px; }
    .ref__box:before {
      content: "";
      position: absolute;
