    .ref__box--active:before {
      opacity: 1; }

.risks {
  margin-bottom: 120px; }
  .risks__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -22px; }
  .risks__item {
    width: 33.25%;
    margin-bottom: 44px;
    padding: 0 22px; }
    .risks__item--all {
      width: 100%; }
  .risks__container {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 365px;
    height: 100%;
    padding: 32px 32px 44px;
    background-color: #F7F8FA;
    border-radius: 40px; }
    .risks__container--all {
      flex-direction: row;
      align-items: center;
      justify-content: flex-start;
      min-height: 0;
      height: auto; }
      .risks__container--all .risks__circle {
        margin-right: 40px; }
      .risks__container--all .risks__text {
        max-width: none; }
  .risks__circle {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 140px;
    height: 140px;
    background-color: #ffffff;
    border-radius: 50%; }
  .risks__title {
    font-weight: bold;
    margin-bottom: 10px;
    font-size: 20px;
    line-height: 30px;
    color: #00A76D; }
  .risks__text {
    max-width: 270px;
    font-weight: 500;
    font-size: 18px;
    line-height: 27px; }
  .risks__button {
    position: absolute;
    top: 34px;
    right: 34px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer; }
  .risks__info {
    position: absolute;
    top: 0;
    left: 0;
    width: calc(200% + 44px);
    height: 100%;
    padding: 32px 32px 16px;
    background-color: #00A76D;
    border-radius: 40px;
