    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    transition: 0.5s; }
    .risks__info--active {
      opacity: 1;
      visibility: visible; }
    .risks__info--left {
      left: auto;
      right: 0; }
    .risks__info--top {
      top: auto;
      bottom: 0;
      width: 100%;
      height: auto; }
  .risks__info-top {
    margin-bottom: 16px;
    padding-bottom: 28px;
