  .risks__info-item {
    position: relative;
    padding-left: 10px;
    font-size: 14px;
    line-height: 20px;
    color: #ffffff; }
    .risks__info-item:not(:last-child) {
      margin-bottom: 12px; }
    .risks__info-item:before {
      content: "";
      position: absolute;
      left: 0;
      top: 7px;
